@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i);
html, body {
    overflow-x: hidden;
    max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



@font-face {
    font-family: GilroyExtraBold;
    src: url(/guillermoroman/static/media/Gilroy-ExtraBold.0f6e082f.otf) format("opentype");
}

@font-face {
    font-family: GilroyLight;
    src: url(/guillermoroman/static/media/Gilroy-Light.c62aded7.otf) format("opentype");
}

.initialRedBox{
	width:50%;
	height:120vh;
	background-color: #ff725c;
	display: inline-block;
	transition: width 0.9s ease 0ms;
	z-index: -5;
}

.expandRedBox {
	width: 100%;
	height:120vh;
	background-color: #ff725c;
	display: inline-block;
	transition: width 0.9s ease 0ms;
	z-index: -5;
}

.helloInitial {
    font-family: GilroyExtraBold;
    font-size: 7vw;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: all 0.9s ease 0ms;
}

.welcomeMessage {
    font-family: GilroyExtraBold;
    font-size: 3vw;
    position: absolute;
    top: 70%;
    left: 25%;
    width: 30%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.helloFinal {
    font-family: GilroyExtraBold;
    font-size: 7vw;
    position: absolute;
    top: 50%;
    left: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: all 0.9s ease 0ms;
}

.initialGrayBox {
    font-family: GilroyExtraBold;
	width:60%;
	height:70%;
	background-color: #1c1b20;
    position: absolute;
    top: 30%;
    right: -100%;
    transition: all 1.5s ease 0ms;
    color: #f4f4f4;
}

.finalGrayBox {
    font-family: GilroyExtraBold;
    width:60%;
    height:70%;
    background-color: #1c1b20;
    position: absolute;
    top: 30%;
    right: 0%;
    transition: all 1.5s ease 0ms;
    color: #f4f4f4;
}

.boxAbout {
    padding-top: 3rem;
    height: 100vh; 
    background-color:#f4f4f4;
}

h3 {
    font-size: 1.4rem;
}

h3 + p {
    font-size: 1.2rem;
    color: #4c4a57;
}


.fixed {
	position: fixed;
}


.Hello {
}

#parallaxAbout {
    font-size: 3vw;
    font-family: GilroyExtraBold;
    background-image: url("https://images.unsplash.com/photo-1532289402244-3cbf8bdeb722?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1650&q=80");
    padding-Top: 50vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.aboutParagraph {
    width: 49%;
    display: inline-block;
    font-size: 2vw;
}

@media (max-width: 600px) {
    h3 {
        font-size: 1rem;
    }

    h3 + p {
        font-size: 0.9rem;
        color: #4c4a57;
    }

    .welcomeMessage {
        font-size: 1.3rem;
    }

    .initialRedBox {
        height:100vh;
    }

    .expandRedBox {
        height:100vh;
    }

    #parallaxAbout {
    font-size: 5vw;
    }

    .aboutParagraph {
        width: 100%;
        font-size: 4.5vw;
        display: block;
    }
}

@media only screen and (max-width: 600px) {
    #parallaxAbout{
        background-image: url("https://images.pexels.com/photos/953214/pexels-photo-953214.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    }
    .boxAbout {
    height: 80vh; 
}
}
